<template>
  <button
    :class="{ 'is-disabled': disabled, small: size == 'small' }"
    class="pkq-btn"
  >
    <slot></slot>
  </button>
</template>
<script>
export default {
  name: "pkq-button",
  props: ["disabled", "size"],
  data() {
    return {};
  },
};
</script>

<style lang="scss">
* {
  padding: 0;
  margin: 0;
}
body {
  font-size: 13px;
  font-family: Roboto;
}
/* button */
.pkq-btn {
  &.small {
    height: 30px;
  }
  color: white;
  padding: 0 15px;
  border: none;
  outline: none;
  height: 34px;
  border-radius: 3px;
  background-image: linear-gradient(to bottom, #1c80d1, #04488c);
}

.pkq-btn.is-disabled {
  background-image: linear-gradient(to top, #8e8e8e, #bebebe);
}
</style>