<template>
  <div class="pkq-dropdown">
    <div ref="elDropdown" @click="toggle" class="dropdown-link">
      <template v-if="$slots.header">
        <slot name="header"></slot>
      </template>
      <div v-else>下拉菜单</div>
      <img
        alt=""
        class="icon-arrow-down"
        src="data:image/svg+xml;base64,"
      />
    </div>
    <div :class="{ hide: !showDrop }" class="dropdown-menu">
      <template v-if="$slots.body">
        <slot name="body"></slot>
      </template>
      <ul v-else>
        <li>Item</li>
        <li>Item</li>
        <li>Item</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "pkq-dropdown",
  data() {
    return {
      showDrop: false,
    };
  },
  methods: {
    toggle() {
      this.showDrop = !this.showDrop;
    },
    hideSel(e) {
      if (this.$refs.elDropdown && !this.$refs.elDropdown.contains(e.target)) {
        this.showDrop = false;
      }
    },
  },
  mounted() {
    document.addEventListener("click", this.hideSel);
  },
  destroyed() {
    document.removeEventListener("click", this.hideSel);
  },
};
</script>

<style lang="scss">
.pkq-dropdown {
  position: relative;
  display: inline-block;
  .dropdown-link {
    cursor: pointer;
    display: flex;
    img.icon-arrow-down {
      margin: 0 5px;
    }
  }
  .dropdown-menu {
    position: absolute;
    top: 20px;
    z-index: 10;
    padding: 10px 0;
    margin: 5px 0;
    background-color: #fff;
    border: 1px solid #ebeef5;
    border-radius: 4px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    transition: all 0.3s;
    min-width: 100%;
    &.hide {
      opacity: 0;
      pointer-events: none;
    }
    ul {
      li {
        padding: 0 15px;
        height: 30px;
        line-height: 30px;
        cursor: pointer;
        &:hover {
          background-color: #ecf5ff;
          color: #66b1ff;
        }
      }
    }
  }
}
</style>