
<template>
  <div ref="elDatePicker" :class="[size]" class="pkq-date-picker">
    <div class="pkq-date-picker_input" @click="toggle">
      <input
        :class="[size]"
        :value="value[0]"
        readonly="readonly"
        type="text"
        class="range-input"
      />
      <span class="pkq-date-picker_separator">-</span>
      <input
        :class="[size]"
        :value="value[1]"
        readonly="readonly"
        type="text"
        class="range-input"
      />
      <i :class="[size]" class="pkq-date-icon"></i>
    </div>
    <div v-if="isShow" class="pkq-date-picker_popper">
      <div class="pkq-date-picker_popper_wrap">
        <div class="pkq-date-Picker_body">
          <div
            v-for="(panel, index) in panels"
            :key="index"
            class="pkq-date-picker_panel"
          >
            <div class="pkq-date-picker_header">
              <input
                :class="[size]"
                readonly="readonly"
                :value="selectDays[index] | date"
                type="text"
                class="currentDate"
              />
              <pkq-select
                :class="[size]"
                v-model="panel.time"
                :lists="selectTimeList"
              ></pkq-select>
            </div>
            <table class="pkq-data-table">
              <thead>
                <tr>
                  <th>
                    <img
                      :class="{
                        disabled: panel.index == 1 && !validStartDate('month'),
                      }"
                      @click="subMonth(panel)"
                      class="pkq-date-picker-arrow"
                      src="data:image/svg+xml;base64,"
                      alt
                    />
                  </th>
                  <th colspan="5">
                    <strong
                      >{{ months[panel.panelMonth] }}
                      {{ panel.panelYear }}</strong
                    >
                  </th>
                  <th>
                    <img
                      :class="{
                        disabled: panel.index == 0 && !validStartDate('month'),
                      }"
                      @click="addMonth(panel)"
                      class="pkq-date-picker-arrow pkq-date-picker-arrow_right"
                      src="data:image/svg+xml;base64,"
                      alt
                    />
                  </th>
                </tr>
                <tr>
                  <th>Su</th>
                  <th>Mo</th>
                  <th>Tu</th>
                  <th>We</th>
                  <th>Th</th>
                  <th>Fr</th>
                  <th>Sa</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(week, index) in panel.weekList" :key="index">
                  <td v-for="(day, j) in week" :key="index + '-' + j">
                    <span
                      v-if="day"
                      @click="selectDay(panel, index * 7 + j)"
                      :class="{
                        'select-date': isSelect(
                          panel.dayFullList[index * 7 + j]
                        ),
                        today: isToday(panel.dayFullList[index * 7 + j]),
                      }"
                      >{{ day != 0 ? day : "" }}</span
                    >
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="pkq-date-Picker_footer">
          <button @click="apply" class="apply">APPLY</button>
          <button @click="cancel" class="cancel">CANCEL</button>
        </div>
      </div>
      <div class="pkq-date-picker_popper_sidebar">
        <button class="pkq-date-picker_shortcut">Today</button>
        <button class="pkq-date-picker_shortcut">Yesterday</button>
        <button class="pkq-date-picker_shortcut">This Week</button>
        <button class="pkq-date-picker_shortcut">Last Week</button>
        <button class="pkq-date-picker_shortcut">This Month</button>
        <button class="pkq-date-picker_shortcut">Last Month</button>
        <button class="pkq-date-picker_shortcut">ALL TIME</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["value", "options", "size"],
  name: "pkq-date-picker",
  data() {
    return {
      months: [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ],
      isShow: false,
      panels: [
        {
          index: 0,
          weekList: [],
          dayList: [], //所有的天数列表，前面空位补0
          dayFullList: [], //所有的天数列表，前面空位补0
          panelYear: "", //仪表盘显示的年
          panelMonth: "", //仪表盘显示的月（从0开始）
          panelDay: "", //仪表盘显示的天（从1开始）
          time: "00:00",
        },
        {
          index: 1,
          weekList: [],
          dayList: [], //所有的天数列表，前面空位补0
          dayFullList: [], //所有的天数列表，前面空位补0
          panelYear: "", //仪表盘显示的年
          panelMonth: "", //仪表盘显示的月（从0开始）
          panelDay: "", //仪表盘显示的天（从1开始）
          time: "23:59",
        },
      ],
      selectDays: [], //选中的日期
    };
  },
  methods: {
    hideSel(e) {
      //this.$refs.elDatePicker.compareDocumentPosition(e.target) < 32 用来区分点击绝对定位的元素(待优化，contains方法判断不了内部被绝对定位的dom元素和e.target的包含关系)
      if (
        !this.$refs.elDatePicker.contains(e.target) &&
        this.$refs.elDatePicker.compareDocumentPosition(e.target) < 32
      ) {
        this.isShow = false;
      }
    },
    toggle() {
      this.isShow = !this.isShow;
      if (this.isShow) this.init();
    },
    apply() {
      this.isShow = false;
      if (this.selectDays && this.selectDays.length == 2) {
        this.$emit("input", [
          this.$options.filters["date"](this.selectDays[0], "yyyy-MM-dd") +
            " " +
            this.panels[0].time,
          this.$options.filters["date"](this.selectDays[1], "yyyy-MM-dd") +
            " " +
            this.panels[1].time,
        ]);
      }
    },
    cancel() {
      this.isShow = false;
    },
    dateInit(
      year = new Date().getFullYear(),
      month = new Date().getMonth(),
      day = new Date().getDate()
    ) {
      let y = year;
      let m = month;
      let lastDay = new Date(y, m + 1, 0).getDate(); //每月最后一天也即每月多少天
      let firstDayisWhat = new Date(y, m, 1).getDay(); //第一天星期几0-6（星期日到星期六）
      let beginTmp = new Array(firstDayisWhat).fill(0); //初始化长度为3的数组并填充0
      let lastTmp = []; //初始化长度为当月天数的数组并填充为0-30或0-31
      let lastFullTmp = []; //初始化完整的月的每天格式为年月日20180627
      for (let i = 1; i <= lastDay; i++) {
        lastTmp.push(i);
        lastFullTmp.push(
          y + "-" + this.addPreZero(m + 1) + "-" + this.addPreZero(i)
        );
      }
      let dayList = [...beginTmp, ...lastTmp]; //用结构的方式生成新日期数组
      let dayFullList = [...beginTmp, ...lastFullTmp]; //用结构的方式生成年月日完整的新日期数组
      let weekList = [];
      for (let j = 0; j < dayList.length; j++) {
        if (weekList[Math.floor(j / 7)]) {
          weekList[Math.floor(j / 7)].push(dayList[j]);
        } else {
          weekList[Math.floor(j / 7)] = [dayList[j]];
        }
      }
      return {
        dayList,
        dayFullList,
        weekList,
        panelYear: y,
        panelMonth: m,
        panelDay: day,
      };
    },
    subMonth(panel) {
      if (this.validStartDate("month") || panel.index == 0) {
        if (panel.panelMonth > 0) {
          panel.panelMonth--;
        } else {
          panel.panelMonth = 11;
          panel.panelYear--;
        }
        panel = Object.assign(
          panel,
          this.dateInit(panel.panelYear, panel.panelMonth, panel.panelDay)
        );
      }
    },
    addMonth(panel) {
      if (this.validStartDate("month") || panel.index == 1) {
        if (panel.panelMonth < 11) {
          panel.panelMonth++;
        } else {
          panel.panelMonth = 0;
          panel.panelYear++;
        }
        panel = Object.assign(
          panel,
          this.dateInit(panel.panelYear, panel.panelMonth, panel.panelDay)
        );
      }
    },
    addPreZero(num) {
      //小于9的需要添加0前缀
      return num > 9 ? num : "0" + num;
    },
    removePreZero(num) {
      //小于9的需要去除0前缀
      return parseInt(num);
    },
    selectDay(panel, index) {
      if (this.selectDays.length == 2) {
        this.selectDays = [];
      }
      this.selectDays.push(panel.dayFullList[index]);
      //排序
      this.selectDays = this.selectDays.sort();
    },
    isSelect(date) {
      let select = false;
      this.selectDays.map((day) => {
        if (this.$options.filters["date"](day, "yyyy-MM-dd") == date) {
          select = true;
        }
      });
      return select;
    },
    isToday(date) {
      return this.$options.filters["date"](new Date(), "yyyy-MM-dd") == date;
    },
    init() {
      let dateFilter = this.$options.filters["date"];
      let dates = this.value;
      if (!Array.isArray(this.value) || this.value.length != 2) {
        dates = [
          dateFilter(new Date(), "yyyy-MM-dd 00:00"),
          dateFilter(new Date(), "yyyy-MM-dd 23:59"),
        ];
        this.$emit("input", dates);
      }
      dates.map((date, index) => {
        if (typeof date == "string" || typeof date == "number")
          date = new Date(date);
        let y = date.getFullYear();
        let m = date.getMonth();
        let d = date.getDate();
        // 开始时间和结束时间是同一个月
        if (
          index == 1 &&
          m == this.panels[0].panelMonth &&
          y == this.panels[0].panelYear
        ) {
          this.subMonth(this.panels[0]);
        }
        this.panels[index].panelYear = y;
        this.panels[index].panelMonth = m;
        this.panels[index].panelDay = d;
        this.panels[index].time = dateFilter(date, "hh:mm"); //当前选中时间
        this.selectDays[index] = date; //标识当前选中日期
      });

      // 初始化日期面板
      this.panels[0] = Object.assign(
        this.panels[0],
        this.dateInit(
          this.panels[0].panelYear,
          this.panels[0].panelMonth,
          this.panels[0].panelDay
        )
      );
      this.panels[1] = Object.assign(
        this.panels[1],
        this.dateInit(
          this.panels[1].panelYear,
          this.panels[1].panelMonth,
          this.panels[1].panelDay
        )
      );
    },
  },
  mounted() {
    this.init();
    document.addEventListener("click", this.hideSel);
  },
  destroyed() {
    document.removeEventListener("click", this.hideSel);
  },
  computed: {
    selectTimeList() {
      var steps = ["00", "30", "59"];
      if (this.options && this.options.steps) {
        steps = this.options.steps;
      }
      let arr = [];
      for (let i = 0; i < 24; i++) {
        steps.map((item) => {
          arr.push(this.addPreZero(i) + ":" + item);
        });
      }
      return arr;
    },
    validStartDate() {
      return (field) => {
        if (field == "month") {
          return (
            this.panels[0].panelYear * 12 + this.panels[0].panelMonth + 1 <
            this.panels[1].panelYear * 12 + this.panels[1].panelMonth
          );
        } else {
          return true;
        }
      };
    },
  },
  filters: {
    date(date, formart = "yyyy-MM-dd") {
      if (!date) return "";
      if (!(date instanceof Date)) date = new Date(date);
      if (/(y+)/.test(formart)) {
        formart = formart.replace(
          RegExp.$1,
          (date.getFullYear() + "").substr(4 - RegExp.$1.length)
        );
      }
      let o = {
        "M+": date.getMonth() + 1,
        "d+": date.getDate(),
        "h+": date.getHours(),
        "m+": date.getMinutes(),
        "s+": date.getSeconds(),
      };
      for (let k in o) {
        if (new RegExp(`(${k})`).test(formart)) {
          let str = o[k] + "";
          formart = formart.replace(
            RegExp.$1,
            RegExp.$1.length === 1 ? str : str > 9 ? str : "0" + str
          );
        }
      }
      return formart;
    },
  },
};
</script>

<style lang="scss">
.pkq-date-picker {
  position: relative;
  &.small {
    .pkq-date-picker_input {
      height: 30px;
    }
  }
  .pkq-date-picker_input {
    -webkit-appearance: none;
    background-color: #fff;
    background-image: none;
    border-radius: 4px;
    border: 1px solid #dcdfe6;
    box-sizing: border-box;
    color: #606266;
    display: inline-block;
    font-size: inherit;
    height: 34px;
    outline: none;
    transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
    width: 310px;
    .range-input {
      &.small {
        height: 28px;
      }
      text-align: center;
      display: inline-block;
      height: 32px;
      width: 130px;
      border: none;
      outline: none;
      vertical-align: middle;
      border-radius: 4px;
    }
    .pkq-date-picker_separator {
      padding: 0 5px;
      vertical-align: middle;
    }
    .pkq-date-icon {
      &.small {
        height: 28px;
      }
      cursor: pointer;
      vertical-align: middle;
      display: inline-block;
      width: 30px;
      height: 32px;
      background-repeat: no-repeat;
      background-position: center center;
      background-image: url("data:image/svg+xml;base64,");
    }
  }

  .pkq-date-picker_popper {
    position: absolute;
    top: 40px;
    left: 0;
    padding: 15px;
    background-color: #fff;
    color: #606266;
    border: 1px solid #e4e7ed;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    margin: 5px 0;
    z-index: 999;
    width: 740px;

    .pkq-date-picker_popper_wrap {
      float: left;
      .pkq-date-Picker_body {
        .pkq-date-picker_panel {
          display: inline-block;
          .pkq-date-picker_header {
            margin-bottom: 5px;
            .currentDate {
              &.small {
                height: 30px;
              }
              width: 100px;
              height: 34px;
              border-radius: 3px;
              background-color: #f4f5f6;
              border: none;
              padding: 0 15px;
              margin-right: 16px;
              margin-left: 15px;
              outline: none;
            }
            .pkq-select {
              &.small {
                .pkq-input {
                  height: 30px;
                }
              }
              width: 120px;
              display: inline-block;
              margin-right: 5px;
            }
          }
          &.pkq-date-picker_panel:nth-child(1) {
            .pkq-data-table,
            .pkq-date-picker_header {
              padding-right: 15px;
            }
          }
          &.pkq-date-picker_panel:nth-child(2) {
            .pkq-data-table,
            .pkq-date-picker_header {
              padding-left: 15px;
              position: relative;
              .pkq-date-picker_header_arrow {
                position: absolute;
                top: 12px;
                left: -10px;
                width: 8px;
                height: 8px;
                border: 2px solid #000;
                border-left: none;
                border-bottom: none;
                transform: rotate(45deg);
              }
            }
            .pkq-data-table {
              border-left: 1px solid #e4e7ed;
            }
          }
          .pkq-data-table {
            td,
            th {
              text-align: center;
              padding: 3px 6px;
            }
            td {
              color: #999999;
              cursor: pointer;
              span {
                display: inline-block;
                width: 28px;
                height: 28px;
                border-radius: 50%;
                vertical-align: middle;
                line-height: 28px;
                user-select: none;
                &.today {
                  color: #409eff;
                  font-weight: 700;
                }
                &:hover {
                  background: #464545;
                  color: #fff;
                  font-weight: normal;
                }
                &.select-date {
                  background: #464545;
                  color: #fff;
                  font-weight: normal;
                }
              }
            }

            th {
              color: #646464;
              strong {
                color: #000;
              }
              .pkq-date-picker-arrow {
                width: 16px;
                height: 16px;
                display: inline-block;
                vertical-align: middle;
                cursor: pointer;
                &.disabled {
                  opacity: 0.5;
                }
              }
              .pkq-date-picker-arrow_right {
                transform: rotate(180deg);
              }
            }
          }
        }
      }
      .pkq-date-Picker_footer {
        padding: 15px;
        text-align: right;
        button {
          outline: none;
          margin-left: 10px;
          border: none;
          width: 70px;
          display: inline-block;
          height: 28px;
          border-radius: 4px;
          cursor: pointer;
          &.apply {
            background-color: #464545;
            color: #fff;
            border: solid 1px #464545;
          }
          &.cancel {
            background-color: #f4f5f6;
            border: solid 1px #e4e5e7;
            color: #999999;
          }
        }
      }
    }
    .pkq-date-picker_popper_sidebar {
      float: left;
      border-left: 1px solid #e4e7ed;
      padding-top: 34px;
      height: 284px;
      margin-right: -15px;
      margin-left: 15px;
      button {
        padding-left: 20px;
        padding-right: 20px;
        outline: none;
        border: none;
        display: block;
        height: 28px;
        background-color: transparent;
        width: 100%;
        text-align: left;
        &:hover {
          background: #d6d6d7;
        }
      }
    }
  }
}
</style>
