<template>
  <div class="pkq-input-wrap">
    <currency-input
      v-if="type == 'currency'"
      :currency="{ currency: 'USD' }"
      :value="value"
      @input="$emit('input', $event)"
      :class="[disabled && 'is-disabled', size]"
      :placeholder="placeholder"
      :disabled="disabled"
      :precision="precision"
      class="pkq-input small"
    />
    <input
      v-else
      :value="value"
      @input="$emit('input', $event.target.value)"
      :class="[disabled && 'is-disabled', size]"
      :placeholder="placeholder"
      :type="inputType"
      :disabled="disabled"
      class="pkq-input small"
    />
  </div>
</template>
<script>
import { CurrencyInput } from "vue-currency-input";
export default {
  name: "pkq-input",
  props: ["value", "disabled", "placeholder", "type", "size", "precision"],
  components: { CurrencyInput },
  data() {
    return {};
  },
  computed: {
    inputType() {
      return this.type == "password" ? "password" : "text";
    },
  },
};
</script>

<style lang="scss">
.pkq-input-wrap {
  &.small {
    .pkq-input {
      height: 30px;
      line-height: 30px;
    }
  }
}
/* input */
.pkq-input {
  &.small {
    height: 30px;
    line-height: 30px;
  }
  -webkit-appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 34px;
  line-height: 34px;
  outline: none;
  padding: 0 15px;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}

.pkq-input:hover {
  border-color: #c0c4cc;
}

.pkq-input.is-disabled {
  background-color: #cecece;
  border-color: #cecece;
  color: #696969;
  cursor: not-allowed;
}
</style>