<template>
  <div
    :class="{ hasImg: hasImg, small: size == 'small' }"
    ref="elSelect"
    class="pkq-select"
  >
    <div
      :class="{ 'is-disabled': disabled }"
      @click="toggle"
      class="select-input"
    >
      <span v-if="hasImg" class="select-img">
        <img :src="currentOption.img" alt />
      </span>
      <input
        :disabled="disabled"
        :class="{ 'is-disabled': disabled }"
        :value="currentOption ? currentOption.label : value"
        placeholder="Select"
        readonly
        class="pkq-input"
        type="text"
      />
      <span class="select-suffix">
        <span class="icon"></span>
      </span>
    </div>
    <ul v-if="isShow && !disabled" class="select-dropdown">
      <li
        @click="change(option)"
        v-for="option in options"
        :key="option.value"
        class="dropdown-item"
      >
        <span v-if="hasImg" class="select-item-img">
          <img :src="option.img" alt />
        </span>
        {{ option.label }}
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "pkq-select",
  props: ["lists", "option", "value", "disabled", "hasImg", "size"],
  data() {
    return {
      isShow: false,
    };
  },
  computed: {
    options() {
      if (Array.isArray(this.lists)) {
        return this.lists.map((item) => {
          if (typeof item != "object") {
            return { value: item, label: item };
          } else {
            return {
              value: item[this.option.value],
              label: item[this.option.label],
              ...item,
            };
          }
        });
      } else {
        return [];
      }
    },
    currentOption() {
      return this.options.find((item) => item.value == this.value);
    },
  },
  methods: {
    toggle() {
      this.isShow = !this.isShow;
    },
    change(option) {
      this.$emit("input", option.value);
      this.isShow = false;
    },
    hideSel(e) {
      if (this.$refs.elSelect && !this.$refs.elSelect.contains(e.target)) {
        this.isShow = false;
      }
    },
  },
  mounted() {
    document.addEventListener("click", this.hideSel);
  },
  destroyed() {
    document.removeEventListener("click", this.hideSel);
  },
};
</script>

<style lang="scss">
/* .pkq-select */
.pkq-select {
  position: relative;
  &.small {
    .pkq-input {
      height: 30px;
    }
  }
  &.hasImg {
    .select-img {
      width: 24px;
      height: 24px;
      display: block;
      position: absolute;
      top: 5px;
      left: 10px;
    }
    .pkq-input {
      padding-left: 40px;
    }
    .dropdown-item {
      position: relative;
      padding-left: 40px;
      .select-item-img {
        width: 24px;
        height: 24px;
        display: block;
        position: absolute;
        left: 10px;
        top: 3px;
      }
    }
  }
}

.select-input {
  position: relative;
  cursor: pointer;
}

.select-input.is-disabled {
  cursor: not-allowed;
}

.select-input .pkq-input {
  display: block;
  width: 100%;
}

.select-input .select-suffix {
  height: 100%;
  width: 30px;
  transition: all 0.5s;
  position: absolute;
  right: 0;
  top: 0;
  display: block;
}

.select-input.open .select-suffix {
  transform: rotate(180deg);
}

.select-input .select-suffix .icon {
  display: block;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-top-color: #3e3e3e;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-5px, -1px);
}

.select-dropdown {
  max-height: 200px;
  overflow-y: auto;
  cursor: pointer;
  list-style: none;
  position: absolute;
  top: 100%;
  margin-top: 10px;
  border: 1px solid #dcdfe6;
  width: 100%;
  border-radius: 4px;
  box-sizing: border-box;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  z-index: 1000;
  background-color: #fff;
}

.dropdown-item {
  line-height: 30px;
  padding: 0 15px;
}

.select-dropdown .dropdown-item:hover {
  background-color: #d6d6d7;
}
</style>