<template>
  <label :class="{'is-disabled':disabled}" class="pkq-checkbox">
    <input
      @change="$emit('input',$event.target.checked)"
      :checked="value"
      :disabled="disabled"
      type="checkbox"
    />
    <span class="checkbox-inner"></span>
    <span class="checkbox-text">{{label}}</span>
  </label>
</template>
<script>
export default {
  name: "pkq-checkbox",
  props: ["value", "label", "disabled"],
  data() {
    return {};
  },
};
</script>

<style lang="scss">
/* checked */
.pkq-checkbox > input[type="checkbox"] {
  display: none;
}

.pkq-checkbox .checkbox-inner {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  border: 1px solid #dcdfe6;
  border-radius: 2px;
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  background-color: #fff;
  z-index: 1;
  transition: border-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46),
    background-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46);
}

.pkq-checkbox > input:checked + .checkbox-inner {
  background-color: #1e68ab;
  border-color: #1e68ab;
}

.pkq-checkbox > input:disabled + .checkbox-inner {
  background-color: #dcdfe6;
}

.pkq-checkbox .checkbox-inner::after {
  box-sizing: content-box;
  content: "";
  border: 2px solid #fff;
  border-left: 0;
  border-top: 0;
  height: 8px;
  left: 5px;
  position: absolute;
  top: 1px;
  transform: rotate(45deg) scaleY(0);
  width: 3px;
  transition: transform 0.15s ease-in 0.05s;
  transform-origin: center;
}

.pkq-checkbox > input:checked + .checkbox-inner::after {
  transform: rotate(45deg) scaleY(1);
}

.pkq-checkbox .checkbox-text {
  vertical-align: middle;
  user-select: none;
  padding-left: 5px;
}
</style>