<template>
  <!-- 侧边菜单栏 -->
  <div :class="{ hidden: !visible }" id="sidebar">
    <!-- logo 图片 -->
    <div class="logo">
      <img
        class="svg-logo"
        src="data:image/svg+xml;base64,"
        alt=""
      />
      <img
        src="data:image/png;base64,"
        alt=""
        class="q-logo"
      />
    </div>
    <!-- 菜单 -->
    <ul class="menus">
      <!-- 遍历菜单 -->
      <li v-for="(menu, index) in menusData" :key="index" class="menu">
        <div @click="menu.isShow = !menu.isShow" class="menu-title">
          <img
            v-if="menu.iconUrl"
            class="menu-icon"
            :src="menu.iconUrl"
            alt=""
          />
          <img
            v-else
            class="menu-icon"
            src="data:image/svg+xml;base64,"
            alt=""
          />
          <span>{{ index + 1 }}. {{ menu.name }}</span>
        </div>
        <ul
          :style="{
            height:
              menu.isShow && visible ? menu.menus.length * 30 + 'px' : '0px',
          }"
          :class="{ hidden: !menu.isShow }"
          class="menu-subs"
        >
          <!-- 遍历子菜单 -->
          <li
            v-for="(submenu, subIndex) in menu.menus"
            :key="subIndex"
            class="menu-sub"
          >
            <a @click="$emit('open-menu', submenu)"
              >{{ index + 1 }}.{{ subIndex + 1 }} {{ submenu.name }}</a
            >
          </li>
        </ul>
      </li>
    </ul>
    <!-- open close 图标 -->
    <div @click="$emit('update:visible', !visible)" class="sidebar-icon">
      <span class="triangle"></span>
    </div>
  </div>
</template>
<script>
export default {
  name: "pkq-side-menu",
  props: ["menus", "visible"],
  data() {
    return {
      menusData: [],
    };
  },
  mounted() {
    this.menus.map((item) => {
      this.menusData.push({ ...item, isShow: item.isShow || false });
    });
  },
};
</script>

<style lang="scss">
ul {
  list-style: none;
}
/* 菜单栏 */
#sidebar {
  width: 250px;
  min-width: 250px;
  min-height: calc(100vh);
  background-color: #242424;
  transition: all 0.5s;
  position: relative;
  &.hidden {
    width: 60px;
    min-width: 60px;
    .logo {
      height: 50px;
      display: flex;
      .svg-logo {
        display: none;
      }
      .q-logo {
        display: block;
        width: 30px;
        margin: auto;
      }
    }
    .menu-title {
      .menu-icon {
        margin-left: 20px !important;
      }
      span {
        opacity: 0;
      }
    }
    .menu-subs {
      height: 0;
    }
    .sidebar-icon {
      transform: rotate(180deg);
      right: 0;
    }
  }
  .logo {
    background: #000;
    display: flex;
    .svg-logo {
      display: block;
      height: 50px;
      width: 50%;
      margin: 0 auto;
      transition: all 0.5s;
    }
    .q-logo {
      display: none;
      transition: all 0.5s;
    }
  }
  .menus {
    li.menu {
      overflow: hidden;
      cursor: pointer;
    }
    .menu-title {
      white-space: nowrap;
      color: #c28a1e;
      font-size: 14px;
      height: 36px;
      line-height: 36px;
      .menu-icon {
        display: inline-block;
        width: 20px;
        margin-left: 15px;
        margin-right: 10px;
        vertical-align: middle;
        height: 36px;
      }
      span {
        transition: all 0.5s;
      }
    }
    .menu-subs {
      transition: all 0.5s;
      &.hidden {
        overflow: hidden;
      }
    }
    .menu-subs > .menu-sub {
      a {
        padding-left: 46px;
        color: #a6a6a6;
        font-size: 13px;
        height: 30px;
        line-height: 30px;
        white-space: nowrap;
        text-decoration: none;
      }
    }
    .menu-subs > .menu-sub.active {
      color: #fff;
    }
  }

  .sidebar-icon {
    position: absolute;
    top: 49%;
    right: -30px;
    width: 30px;
    height: 26px;
    border-radius: 3px;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.3);
    background-color: #5e5d5f;
    text-align: center;
    cursor: pointer;
    .triangle {
      display: inline-block;
      margin: auto;
      border: 8px solid transparent;
      border-right-color: #c8c7c8;
      margin-left: -12px;
      margin-top: 5px;
    }
  }
}
/* hidden动画 */
@keyframes hidden {
  0% {
    display: block;
  }
  50% {
    display: block;
  }

  100% {
    display: none;
  }
}
</style>