<template>
  <!-- 消息弹窗 -->
  <div v-if="visible" class="message-dialog">
    <div class="dialog-wrap">
      <div class="message-header">
        <!--头部slot -->
        <slot v-if="$slots.header" name="header"></slot>
        <div v-else class="text">ERROR MESSAGE</div>
        <div @click="$emit('update:visible', false)" class="close">X</div>
      </div>
      <div class="message-body">
        <slot v-if="$slots.body" name="body"></slot>
        <template v-else
          >Connection could not be made.Connection could not be made. Connection
          could not be made.</template
        >
      </div>
      <slot v-if="$slots.footer" name="footer"></slot>

      <button v-else @click="$emit('update:visible', false)" class="pkq-btn">
        Comfrim
      </button>
    </div>
  </div>
</template>
<script>
export default {
  name: "pkq-message-dialog",
  props: ["visible"],
  data() {
    return {};
  },
};
</script>

<style lang="scss">
/* message 弹窗 */
.message-dialog {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99999;
  display: flex;
  transition: all 0.5s;
  .dialog-wrap {
    margin: auto;
    width: 370px;
    height: 291px;
    border-radius: 4px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.15);
    background-color: #fbfbfb;
    .message-header {
      display: flex;
      height: 40px;
      line-height: 40px;
      color: #fff;
      font-size: 15px;
      background-image: linear-gradient(to bottom, #383838 -33%, #272727 67%);
      .text {
        flex: 1;
        padding-left: 15px;
      }
      .close {
        width: 50px;
        text-align: center;
      }
    }
    .message-body {
      padding: 15px;
      height: 160px;
      font-size: 13px;
      color: #242424;
    }
    & > .pkq-btn {
      color: #fff;
      font-size: 15px;
      width: 80%;
      margin: 0 auto;
      display: block;
      border-radius: 3px;
      background-image: linear-gradient(to top, #727272, #909090);
    }
  }
}
</style>