<template>
  <!-- 侧边栏弹窗 -->
  <div :class="{ hidden: !visible }" class="side-dialog">
    <div class="wrap">
      <div class="dialog-header">
        <!--头部slot -->
        <slot v-if="$slots.header" name="header"></slot>
        <div v-else class="text">Change Password</div>
        <a @click="$emit('update:visible', false)" class="close">X</a>
      </div>
      <div class="dialog-body">
        <slot v-if="$slots.body" name="body"></slot>
        <template v-else>
          <div v-for="item in 10" :key="item" class="form-group">
            <label>Login ID</label>
            <input type="text" class="pkq-input small" />
          </div>
          <div class="form-group">
            <label>Merchant ID</label>
            <pkq-select size="small" :lists="[1, 2, 3, 4]"> </pkq-select>
          </div>
        </template>
      </div>
      <div class="dialog-footer">
        <slot v-if="$slots.footer" name="footer"></slot>
        <template v-else>
          <button
            @click="$emit('update:visible', false)"
            class="pkq-btn cancel"
          >
            Cancel
          </button>
          <button
            @click="$emit('update:visible', false)"
            class="pkq-btn comfirm"
          >
            Submit
          </button>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "pkq-side-dialog",
  props: ["visible"],
  data() {
    return {};
  },
  mounted() {
    console.log(this.$slots);
  },
  watch: {
    visible(newVal) {
      // 当有遮罩层时，body滚动条消失，防止出现双滚动条
      if (newVal) {
        document.body.style = "overflow:hidden";
      } else {
        document.body.style = "overflow:auto";
      }
    },
  },
};
</script>

<style lang="scss">
/* 侧边栏弹窗 */
.side-dialog {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 9999;
  display: flex;
  justify-content: flex-end;
  transition: all 0.5s;
  &.hidden {
    pointer-events: none;
    background: rgba(0, 0, 0, 0);
    .wrap {
      margin-right: -400px;
    }
  }
}
.side-dialog .wrap {
  width: 360px;
  background-color: #fbfbfb;
  transition: all 0.5s;
  .dialog-header {
    height: 50px;
    line-height: 50px;
    font-size: 15px;
    background-image: linear-gradient(to bottom, #383838, #272727);
    color: #fff;
    display: flex;
    .text {
      flex: 1;
      padding-left: 15px;
    }
    .close {
      width: 50px;
      text-align: center;
      font-size: 18px;
      cursor: pointer;
    }
  }
  .dialog-body {
    height: calc(100vh - 160px);
    min-height: 200px;
    padding: 15px 30px;
    overflow: auto;
    .form-group > label {
      line-height: 24px;
    }
    .form-group > .pkq-select {
      width: 100%;
      display: block;
    }
  }

  .dialog-footer {
    border-top: solid 1px #d9d9d9;
    height: 80px;
    line-height: 80px;
    background-color: #f4f5f6;
    text-align: center;
    .pkq-btn {
      width: 150px;
      margin-right: 10px;
    }
    .pkq-btn.cancel {
      background-image: linear-gradient(to top, #8e8e8e, #bebebe);
    }
  }
}
</style>