<template>
  <!-- loading 遮罩层 -->
  <div v-if="visible" class="loading">
    <img
      src="data:image/svg+xml;base64,"
      class="loading-icon"
    />
  </div>
</template>
<script>
export default {
  name: "loading",
  props: ["visible"],
  data() {
    return {};
  },
};
</script>

<style lang="scss">
/* 旋转动画 */
@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
/* loading */
.loading {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999999;
  display: flex;
  .loading-icon {
    margin: auto;
    display: block;
    transition: 0.8s;
    animation: rotate 0.5s linear infinite;
  }
}
</style>